const getPickerAsyncData = async function(obj) { // picker-custompicker-custom2
	let _this = this;
	_this.waiting = true; // 
	const {
		value, // pickervalue
		index, // picker
		launch, // 
		datas, // picker
		oldValue, // pickervalue
		customId, // 
		linkageNum // 
	} = obj; // pickervalue  : {data_0: , data_1: , data_2: }, pickervalue, 
	const {
		data_0, // 
		data_1 // 
	} = datas; // : {data_0: , data_1: , data_2: }

	// sendData, async_1async_2
	let sendData = {};
	try {
		switch (linkageNum) {
			case 2: // 
				if (!!_this.memoryData_1[value[0]]) { // 
					_this.async_1 = [..._this.memoryData_1[value[0]]]; // 
				} else {
					sendData = getSendData(obj); // 
					const newData_1 = await getAsyncDataFc(sendData); // 
					_this.async_1 = [...newData_1]; // 
					_this.memoryData_1[value[0]] = [...newData_1]; // 
				}
				if (launch) { //  valuedefaultValue
					_this.value[1] = value[1]; // 
				} else {
					_this.value[1] = 0; // value0
				}
				break;
			case 3: // 
				if (launch) { //  valuedefaultValue
					// 
					sendData = getSendData({ ...obj,
						column: 0
					}); // 
					const newData_1 = await getAsyncDataFc(sendData); // 
					_this.async_1 = [...newData_1]; // 
					_this.memoryData_1[value[0]] = [...newData_1]; // 
					_this.value[1] = value[1]; // 
					// 
					sendData = getSendData({ ...obj,
						datas: { ...datas,
							data_1: [...newData_1]
						},
						column: 1
					}); // 
					const newData_2 = await getAsyncDataFc(sendData);
					_this.async_2 = [...newData_2]; // 
					if (!_this.memoryData_2[value[0]])
						_this.memoryData_2[value[0]] = [];
					_this.memoryData_2[value[0]][value[1]] = [...newData_2]; // 
					_this.value[2] = value[2]; // 
				} else { // picker
					let newData_1;
					let newData_2;
					switch (index) {
						case 0: // 
							// 
							if (!!_this.memoryData_1[value[0]]) { // 
								newData_1 = [..._this.memoryData_1[value[0]]]; // 
							} else {
								sendData = getSendData({ ...obj,
									column: 0
								}); // 
								newData_1 = await getAsyncDataFc(sendData);
								_this.memoryData_1[value[0]] = [...newData_1]; // 
							}
							_this.async_1 = [...newData_1]; // 
							_this.value[1] = 0; // 

							// 
							if (!_this.memoryData_2[value[0]])
								_this.memoryData_2[value[0]] = [];
							if (!!_this.memoryData_2[value[0]][value[1]]) { // 
								newData_2 = [..._this.memoryData_2[value[0]][value[1]]]; // 
							} else {
								sendData = getSendData({ ...obj,
									datas: { ...datas,
										data_1: [...newData_1]
									},
									column: 1
								}); // 
								newData_2 = await getAsyncDataFc(sendData);
								_this.memoryData_2[value[0]][value[1]] = [...newData_2]; // 
							}
							_this.async_2 = [...newData_2] // 
							_this.value[2] = 0; // 
							break;
						case 1: // 
							log('')
							if (!_this.memoryData_2[value[0]])
								_this.memoryData_2[value[0]] = [];
							if (!!_this.memoryData_2[value[0]][value[1]]) { // 
								log('');
								newData_2 = [..._this.memoryData_2[value[0]][value[1]]]; // 
							} else {
								log('');
								sendData = getSendData(obj); // 
								newData_2 = await getAsyncDataFc(sendData);
								_this.memoryData_2[value[0]][value[1]] = [...newData_2]; // 
							}
							_this.async_2 = [...newData_2]; // 
							_this.value[2] = 0; // 
							break;
						default:
							break;
					}
				}
				break;
		}
		_this.oldValue = [...this.value];
		_this.waiting = false;
	} catch (e) {
		//TODO handle the exception
		_this.oldValue = [...this.value];
		_this.waiting = false;

	}
}

