const UpLoadFile = function(customId, filePath) { // : (, )
	if (filePath.substring(0, 4) === 'http') { //: ,  resolve. customId, 
		const replacePath = filePath.replace(interfaces.baseUrl, '');
		return Promise.resolve({
			data: replacePath
		});
	}
	let _this = this;
	let url = '';
	let formData = {};
	let name = '';
	switch (customId) { //picsUpLoadFileType, urlformDataname
		case 'UpLoadImage_1': //
			url = '';
			formData = {};
			name = '';
			break;
		default: //
			url = interfaces.upLoadImg;
			formData = {};
			name = '';
			break;
	}
	if (!url) {
		_this.showToast('url');
		return new Promise((rs, rj) => {
			rj('url');
		});
	}
	if (!filePath) {
		_this.showToast('filePath');
		return new Promise((rs, rj) => {
			rj('filePath');
		});
	}
	return new Promise((reslove, reject) => {
		_this.showLoading('');
		const obj = {
			url,
			name,
			filePath,
			success(res) {
				appJS.log('UpLoadFilesuccess: ' + JSON.stringify(res));
				_this.hideLoading();
				reslove(res)
			},
			fail(err) {
				appJS.log('UpLoadFilefail')
				appJS.log(JSON.stringify(err))
				_this.hideLoading();
				reject();
			}
		}









		obj.formData = formData;

		uni.uploadFile(obj);
	})
}

const pics_splice = function(vals, val) { // , vals val
	if (typeof(vals) !== 'string') { // 
		vals = val || '|'; // 
		return vals;
	}
	vals += val ? '|' + val : '|';
	return vals; // return vals
}


const filterTypeObj = { // 
	twoDecimalPlaces(value) { // 
		value = value.replace(/[^\d.]/g, ""); //.
		value = value.replace(/\.{2,}/g, "."); //. 
		value = value.replace(/^(\-)*(\d+)\.(\d).*$/, '$1$2.$3'); //1 
		return value; // return value
	}
};

